.body {
  min-height: 100vh;
  color: #939393;
  font-family: Lato, "Helvetica Neue", Helvetica, Arial, sans-serif;
}

.row {
  display: flex;
  flex-wrap: wrap;
}

a {
  color: #939393;
}

.right {
  width: 60%;
  max-width: 900px;
}

.left {
  width: 40%;
}

.background {
  background-color: #fbfbfb;
}

.occupation {
  font-weight: bold;
}

.left-1 {
  min-height: 100vh;
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
}

.with-padding {
  padding: 50px;
}

.name {
  color: #939393;
  font-size: 78px;
  font-weight: 700;
  margin: 12px 0;
  color: #f8bb10;
}

.title {
  color: #939393;
  font-size: 24px;
  margin: 12px 0;
  font-weight: 300;
}

.photo {
  /* padding-right: 100px; */
  width: 80%;
}
.photo img {
  border-radius: 50%;
  width: 100%;
}

.intro {
  min-height: 100vh;
  box-sizing: border-box;
  display: flex;
  justify-content: space-between;
  flex-direction: column;
}

.intro-footer {
  flex-wrap: wrap;
  border-top: solid 2px #ececec;
  padding-top: 24px;
  display: flex;
  justify-content: space-between;
}

.intro-footer > * {
  margin-bottom: 12px;
}

h4 {
  color: #727272;
  margin: 0 0 6px 0;
}

h2 {
  color: #727272;
  font-size: 18px;
}

.text-right {
  text-align: right;
}
.sub-title {
  font-size: 14px;
}
.content {
  font-size: 17px;
}
.s-content {
  margin-top: 12px;
  font-size: 15px;
}

.no-margin {
  margin: 0;
}

p {
  margin: 0 0 20px 0;
}

h2 {
  margin: 0 12px 8px 0;
}

.card {
  margin-bottom: 36px;
}

@media only screen and (max-width: 480px) {
  /* Style adjustments for viewports that meet the condition */
  .right {
    width: 100%;
    max-width: 900px;
  }

  .left {
    width: 100%;
  }

  .left-1 {
    display: none;
  }

  .mobile-text-left {
    text-align: left;
  }
}
